'use client';

import { useState } from 'react';
import { useAdminStore } from '../store/adminStore';
import { Pencil } from 'lucide-react';

type Jogador = {
  id: string;
  nome: string;
  pontos: number;
  saldoGols: number;
  vitorias: number;
  derrotas: number;
};

type Partida = {
  id: string;
  time1: string;
  time2: string;
  golsTime1: number;
  golsTime2: number;
  data: string;
};

const jogadoresExemplo: Jogador[] = [
  { id: '1', nome: 'Breno', pontos: 12, saldoGols: 5, vitorias: 4, derrotas: 1 },
  { id: '2', nome: 'João', pontos: 9, saldoGols: 2, vitorias: 3, derrotas: 2 },
  { id: '3', nome: 'Carlos', pontos: 6, saldoGols: 0, vitorias: 2, derrotas: 3 },
];

const partidasExemplo: Partida[] = [
  { id: '1', time1: 'Breno', time2: 'João', golsTime1: 3, golsTime2: 2, data: '2025-04-28' },
  { id: '2', time1: 'Carlos', time2: 'João', golsTime1: 1, golsTime2: 1, data: '2025-05-05' },
];

export default function HomePage() {
  const [ordenarPor, setOrdenarPor] = useState<'pontos' | 'saldoGols' | 'vitorias' | 'derrotas'>('pontos');
  const [mostrarModalJogador, setMostrarModalJogador] = useState(false);
  const [mostrarModalPartida, setMostrarModalPartida] = useState(false);
  const [jogadorEditando, setJogadorEditando] = useState<Jogador | null>(null);
  const [partidaEditando, setPartidaEditando] = useState<Partida | null>(null);
  const [modoAdicionar, setModoAdicionar] = useState<boolean>(true);
  const isAdmin = useAdminStore((state) => state.isAdmin);

  function handleEditarJogador(jogador: Jogador) {
    setModoAdicionar(false);
    setJogadorEditando(jogador);
    setMostrarModalJogador(true);
  }

  function handleEditarPartida(partida: Partida) {
    setPartidaEditando(partida);
    setMostrarModalPartida(true);
  }

  const jogadoresOrdenados = [...jogadoresExemplo].sort((a, b) => {
    return b[ordenarPor] - a[ordenarPor];
  });

  return (
    <main className="min-h-screen bg-gray-100 p-6">
      <h1 className="text-3xl font-bold text-center text-blue-700 mb-6">Ranking da Pelada ⚽</h1>

      {/* Botões de Ordenação */}
      <div className="flex justify-center gap-4 mb-6 flex-wrap">
        <button onClick={() => setOrdenarPor('pontos')} className={`px-4 py-2 rounded ${ordenarPor === 'pontos' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600 cursor-pointer'}`}>Pontos</button>
        <button onClick={() => setOrdenarPor('saldoGols')} className={`px-4 py-2 rounded ${ordenarPor === 'saldoGols' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600 cursor-pointer'}`}>Saldo de Gol</button>
        <button onClick={() => setOrdenarPor('vitorias')} className={`px-4 py-2 rounded ${ordenarPor === 'vitorias' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600 cursor-pointer'}`}>Vitórias</button>
        <button onClick={() => setOrdenarPor('derrotas')} className={`px-4 py-2 rounded ${ordenarPor === 'derrotas' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600 cursor-pointer'}`}>Derrotas</button>
      </div>

      {/* Tabela de Jogadores */}
      <div className="overflow-x-auto">
        <table className="min-w-full bg-white rounded shadow-md">
          <thead>
            <tr className="bg-blue-600 text-white">
              <th className="py-2 px-4">#</th>
              <th className="py-2 px-4">Nome</th>
              <th className="py-2 px-4">Pontos</th>
              <th className="py-2 px-4">Saldo de Gol</th>
              <th className="py-2 px-4">Vitórias</th>
              <th className="py-2 px-4">Derrotas</th>
            </tr>
          </thead>
          <tbody>
            {jogadoresOrdenados.map((jogador, index) => (
              <tr key={jogador.id} className="text-center border-b">
                <td className="py-2 px-4 font-semibold text-gray-800">{index + 1}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.nome}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.pontos}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.saldoGols}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.vitorias}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.derrotas}
                  {isAdmin && (
                    <button onClick={() => handleEditarJogador(jogador)} className="text-blue-600 hover:text-blue-800">
                      <Pencil size={16} />
                    </button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Tabela de Partidas */}
      <div className="overflow-x-auto mt-8">
        <table className="min-w-full bg-white rounded shadow-md">
          <thead>
            <tr className="bg-green-600 text-white">
              <th className="py-2 px-4">#</th>
              <th className="py-2 px-4">Time 1</th>
              <th className="py-2 px-4">Time 2</th>
              <th className="py-2 px-4">Gols Time 1</th>
              <th className="py-2 px-4">Gols Time 2</th>
              <th className="py-2 px-4">Data</th>
            </tr>
          </thead>
          <tbody>
            {partidasExemplo.map((partida, index) => (
              <tr key={partida.id} className="text-center border-b">
                <td className="py-2 px-4 font-semibold text-gray-800">{index + 1}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{partida.time1}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{partida.time2}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{partida.golsTime1}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{partida.golsTime2}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{partida.data}
                  {isAdmin && (
                    <button onClick={() => handleEditarPartida(partida)} className="text-blue-600 hover:text-blue-800">
                      <Pencil size={16} />
                    </button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Botões para Adicionar Jogador e Partida */}
      {isAdmin && (
        <div className="fixed bottom-6 right-6 flex gap-4">
          <button 
            onClick={() => {
              setJogadorEditando(null);  // Limpa o jogador que estava editando
              setModoAdicionar(true);    // Define que estamos no modo de adicionar
              setMostrarModalJogador(true);  // Abre o modal
            }} 
            className="bg-green-600 text-white px-4 py-2 rounded-full shadow-lg hover:bg-green-700"
          >
            + Jogador
          </button>


          <button onClick={() => setMostrarModalPartida(true)} className="bg-yellow-600 text-white px-4 py-2 rounded-full shadow-lg hover:bg-yellow-700">
            + Partida
          </button>
        </div>
      )}

      {/* Modal para Adicionar Jogador */}
      {mostrarModalJogador && (
        <div className="fixed inset-0 flex justify-center items-center z-50">
          <div className="bg-white p-6 rounded shadow-lg w-96">
            <h2 className="py-2 px-4 font-semibold text-gray-800">
              {modoAdicionar ? 'Adicionar jogador' : 'Editar jogador'}
            </h2>

            <input
              type="text"
              placeholder="Nome"
              value={jogadorEditando?.nome || ''} // Se não houver jogadorEditando, usa uma string vazia
              onChange={(e) => setJogadorEditando((prev) => prev ? { ...prev, nome: e.target.value } : { id: '', nome: e.target.value, pontos: 0, saldoGols: 0, vitorias: 0, derrotas: 0 })}
              className="w-full mt-2 p-2 border border-gray-300 rounded"
            />

            <input
              type="number"
              placeholder="Pontos"
              value={jogadorEditando?.pontos || 0}
              onChange={(e) => setJogadorEditando((prev) => prev ? { ...prev, pontos: parseInt(e.target.value, 10) } : { id: '', nome: '', pontos: parseInt(e.target.value, 10), saldoGols: 0, vitorias: 0, derrotas: 0 })}
              className="w-full mt-2 p-2 border border-gray-300 rounded"
            />

            <input
              type="number"
              placeholder="Saldo de Gol"
              value={jogadorEditando?.saldoGols || 0}
              onChange={(e) => setJogadorEditando((prev) => prev ? { ...prev, saldoGols: parseInt(e.target.value, 10) } : { id: '', nome: '', pontos: 0, saldoGols: parseInt(e.target.value, 10), vitorias: 0, derrotas: 0 })}
              className="w-full mt-2 p-2 border border-gray-300 rounded"
            />

            <input
              type="number"
              placeholder="Vitórias"
              value={jogadorEditando?.vitorias || 0}
              onChange={(e) => setJogadorEditando((prev) => prev ? { ...prev, vitorias: parseInt(e.target.value, 10) } : { id: '', nome: '', pontos: 0, saldoGols: 0, vitorias: parseInt(e.target.value, 10), derrotas: 0 })}
              className="w-full mt-2 p-2 border border-gray-300 rounded"
            />

            <input
              type="number"
              placeholder="Derrotas"
              value={jogadorEditando?.derrotas || 0}
              onChange={(e) => setJogadorEditando((prev) => prev ? { ...prev, derrotas: parseInt(e.target.value, 10) } : { id: '', nome: '', pontos: 0, saldoGols: 0, vitorias: 0, derrotas: parseInt(e.target.value, 10) })}
              className="w-full mt-2 p-2 border border-gray-300 rounded"
            />

            <div className="mt-4 flex justify-between">
              <button onClick={() => setMostrarModalJogador(false)} className="bg-gray-600 text-white px-4 py-2 rounded">Cancelar</button>
              <button className="bg-blue-600 text-white px-4 py-2 rounded">
                {jogadorEditando ? 'Salvar' : 'Adicionar'}
              </button>
            </div>

          </div>
        </div>
      )}

      {/* Modal para Adicionar Partida */}
      {mostrarModalPartida && (
        <div className="fixed inset-0 flex justify-center items-center z-50">
          <div className="bg-white p-6 rounded shadow-lg w-96">
            <h2 className="py-2 px-4 font-semibold text-gray-800">
              {partidaEditando ? 'Editar Partida' : 'Adicionar Partida'}
            </h2>

            <input
              type="text"
              placeholder="Time 1"
              value={partidaEditando?.time1 || ''}
              onChange={(e) => setPartidaEditando((prev) => prev ? { ...prev, time1: e.target.value } : { id: '', time1: e.target.value, time2: '', golsTime1: 0, golsTime2: 0, data: '' })}
              className="w-full mt-2 p-2 border border-gray-300 rounded"
            />
            {/* Outros campos para editar informações da partida */}

            <div className="mt-4 flex justify-between">
              <button onClick={() => setMostrarModalPartida(false)} className="bg-gray-600 text-white px-4 py-2 rounded">Cancelar</button>
              <button className="bg-blue-600 text-white px-4 py-2 rounded">
                {partidaEditando ? 'Salvar' : 'Adicionar'}
              </button>
            </div>
          </div>
        </div>
      )}
    </main>
  );
}
